import { Utils } from "./Utils";
class UtilsGMCookie {
    windowApi = {
        window: window,
        document: document,
    };
    constructor(windowApiOption) {
        if (windowApiOption) {
            this.windowApi = Object.assign({}, windowApiOption);
        }
    }
    /**
     * 获取单个cookie
     * @param cookieName cookie名
     */
    get(cookieName) {
        if (typeof cookieName !== "string") {
            throw new TypeError("Utils.GMCookie.get 参数cookieName 必须为字符串");
        }
        let cookies = this.windowApi.document.cookie.split(";");
        let findValue = void 0;
        for (const cookieItem of cookies) {
            let item = cookieItem.trim();
            let itemSplit = item.split("=");
            let itemName = itemSplit[0];
            itemSplit.splice(0, 1);
            let itemValue = decodeURIComponent(itemSplit.join(""));
            if (itemName === cookieName) {
                findValue = {
                    domain: this.windowApi.window.location.hostname,
                    expirationDate: null,
                    hostOnly: true,
                    httpOnly: false,
                    name: cookieName,
                    path: "/",
                    sameSite: "unspecified",
                    secure: true,
                    session: false,
                    value: itemValue,
                };
                break;
            }
        }
        return findValue;
    }
    /**
     *  获取多组Cookie
     * @param paramDetails 配置
     * @param callback 获取操作后的回调
     * + cookies object[]
     * + error string|undefined
     **/
    list(paramDetails, callback) {
        if (paramDetails == null) {
            throw new Error("Utils.GMCookie.list 参数不能为空");
        }
        let resultData = [];
        try {
            let details = {
                url: this.windowApi.window.location.href,
                domain: this.windowApi.window.location.hostname,
                name: "",
                path: "/",
            };
            details = Utils.assign(details, paramDetails);
            let cookies = this.windowApi.document.cookie.split(";");
            cookies.forEach((item) => {
                item = item.trim();
                let itemSplit = item.split("=");
                let itemName = itemSplit[0];
                itemSplit.splice(0, 1);
                let itemValue = decodeURIComponent(itemSplit.join(""));
                let nameRegexp = details.name instanceof RegExp
                    ? details.name
                    : new RegExp("^" + details.name, "g");
                if (itemName.match(nameRegexp)) {
                    resultData.push({
                        domain: this.windowApi.window.location.hostname,
                        expirationDate: null,
                        hostOnly: true,
                        httpOnly: false,
                        name: itemName,
                        path: "/",
                        sameSite: "unspecified",
                        secure: true,
                        session: false,
                        value: itemValue,
                    });
                }
            });
            if (typeof callback === "function") {
                callback(resultData);
            }
        }
        catch (error) {
            if (typeof callback === "function") {
                callback(resultData, error);
            }
        }
    }
    /**
     *  获取多组Cookie
     * @param paramDetails 配置
     **/
    getList(paramDetails) {
        if (paramDetails == null) {
            throw new Error("Utils.GMCookie.list 参数不能为空");
        }
        let resultData = [];
        let details = {
            url: this.windowApi.window.location.href,
            domain: this.windowApi.window.location.hostname,
            name: "",
            path: "/",
        };
        details = Utils.assign(details, paramDetails);
        let cookies = this.windowApi.document.cookie.split(";");
        cookies.forEach((item) => {
            item = item.trim();
            let itemSplit = item.split("=");
            let itemName = itemSplit[0];
            itemSplit.splice(0, 1);
            let itemValue = decodeURIComponent(itemSplit.join(""));
            let nameRegexp = details.name instanceof RegExp
                ? details.name
                : new RegExp("^" + details.name, "g");
            if (itemName.match(nameRegexp)) {
                resultData.push({
                    domain: this.windowApi.window.location.hostname,
                    expirationDate: null,
                    hostOnly: true,
                    httpOnly: false,
                    name: itemName,
                    path: "/",
                    sameSite: "unspecified",
                    secure: true,
                    session: false,
                    value: itemValue,
                });
            }
        });
        return resultData;
    }
    /**
     * 设置Cookie
     * @param paramDetails 配置
     * @param callback 设置操作后的回调(成功/失败)
     */
    set(paramDetails, callback = (error) => { }) {
        try {
            let details = {
                url: this.windowApi.window.location.href,
                name: "",
                value: "",
                domain: this.windowApi.window.location.hostname,
                path: "/",
                secure: true,
                httpOnly: false,
                /**
                 * Expires in 30 days
                 */
                expirationDate: Math.floor(Date.now()) + 60 * 60 * 24 * 30,
            };
            details = Utils.assign(details, paramDetails);
            let life = details.expirationDate
                ? details.expirationDate
                : Math.floor(Date.now()) + 60 * 60 * 24 * 30;
            let cookieStr = details.name +
                "=" +
                decodeURIComponent(details.value) +
                ";expires=" +
                new Date(life).toGMTString() +
                "; path=/";
            this.windowApi.document.cookie = cookieStr;
            callback();
        }
        catch (error) {
            callback(error);
        }
    }
    /**
     * 删除Cookie
     * @param paramDetails 配置
     * @param callback 删除操作后的回调(成功/失败)
     */
    delete(paramDetails, callback = (error) => { }) {
        try {
            let details = {
                url: this.windowApi.window.location.href,
                name: "",
                // @ts-ignore
                firstPartyDomain: "",
            };
            details = Utils.assign(details, paramDetails);
            let cookieStr = details.name + "=; expires=Thu, 01 Jan 1970 00:00:00 UTC; path=/;";
            this.windowApi.document.cookie = cookieStr;
            callback();
        }
        catch (error) {
            callback(error);
        }
    }
}
export { UtilsGMCookie };
